<template>
  <div>
    <h1>home</h1>
    <div>
      <p>
        <label for="phone">핸드폰 번호 ( '-' 없이 숫자만 입력해주세요 )</label>
      </p>
      <p>
        <input
          type="number"
          inputmode="numeric"
          pattern="[0-9]*"  
          id="phone"
          v-model="inputPhoneNumber"
          @input="handleInput"
          :maxlength="11"
        >
      </p>
      <button @click="findPhone">조회</button>
    </div>
    <!-- <ul>
      <li v-for="(item, index) in list" :key="index">{{ item }}</li>
    </ul> -->
  </div>
</template>

<script>
import data from "@/assets/data.json";
export default {
  data() {
    return {
      list: [],
      inputPhoneNumber: "",
      foundPhone: null,
      checkPhone: localStorage.getItem('phone', phoneValue)
    };
  },
  created() {
    this.list = data;
  },
  methods: {
    handleInput() {
      // 입력 필드 내에서 숫자만 남기고 나머지 문자 제거
      this.inputPhoneNumber = this.inputPhoneNumber.replace(/[^0-9]/g, "");
    },
    findPhone() {
      const phoneNumberToFind = this.inputPhoneNumber;
      const foundItem = this.list.find(item => item.phone === phoneNumberToFind);
      if (foundItem) {
        this.foundPhone = foundItem.phone;
        this.$router.push(`/check?phone=${encodeURIComponent(this.foundPhone)}`)
      } else {
        this.$router.push(`/fail`)
      }
    },
  },
};
</script>

<style>
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
</style>
